define('camunda-commons-ui/util/index',[
  'angular',
  './uriFilter',
  './uriProvider',
  './notifications'
], function(
  angular,
  uriFilter,
  uriProvider,
  notifications
) {
  'use strict';

  /**
   * @module cam.commons.util
   */

  /**
   * @memberof cam.commons
   */

  return angular.module('cam.commons.util', [])
    .filter('uri', uriFilter)
    .provider('Uri', uriProvider)
    .service('Notifications', notifications);
});

define('camunda-commons-ui/util', ['camunda-commons-ui/util/index'], function (main) { return main; });

